* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --primary: #1E40AF;
  --secondary: #F97316;
  --success: #10B981;
  --dark: #111827;
  --light: #F9FAFB;
  --gray: #6B7280;
  --border: #E5E7EB;
  --shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  --shadow-lg: 0 20px 25px rgba(0, 0, 0, 0.15);
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  line-height: 1.6;
  color: var(--dark);
  background: white;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

/* ===== NAVBAR ===== */
.navbar {
  position: sticky;
  top: 0;
  background: white;
  box-shadow: var(--shadow);
  z-index: 1000;
  border-bottom: 2px solid var(--primary);
}

.navbar-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 15px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  font-size: 24px;
  font-weight: 800;
  color: var(--primary);
  transition: transform 0.3s;
}

.logo:hover {
  transform: scale(1.05);
}

.nav-menu {
  display: flex;
  gap: 25px;
  list-style: none;
}

.nav-menu a {
  color: var(--dark);
  text-decoration: none;
  font-weight: 600;
  cursor: pointer;
  transition: color 0.3s;
  position: relative;
}

.nav-menu a:hover {
  color: var(--primary);
}

.nav-menu a::after {
  content: '';
  position: absolute;
  bottom: -5px;
  left: 0;
  width: 0;
  height: 3px;
  background: var(--primary);
  transition: width 0.3s;
}

.nav-menu a:hover::after {
  width: 100%;
}

.hamburger {
  display: none;
  flex-direction: column;
  gap: 6px;
  background: none;
  border: none;
  cursor: pointer;
}

.hamburger span {
  width: 25px;
  height: 3px;
  background: var(--primary);
  transition: all 0.3s;
  border-radius: 2px;
}

.hamburger.active span:nth-child(1) {
  transform: rotate(45deg) translate(8px, 8px);
}

.hamburger.active span:nth-child(2) {
  opacity: 0;
}

.hamburger.active span:nth-child(3) {
  transform: rotate(-45deg) translate(7px, -7px);
}

@media (max-width: 768px) {
  .hamburger {
    display: flex;
  }

  .nav-menu {
    position: fixed;
    left: -100%;
    top: 60px;
    flex-direction: column;
    gap: 0;
    background: white;
    width: 100%;
    text-align: center;
    transition: 0.3s;
    padding: 20px 0;
    box-shadow: var(--shadow-lg);
  }

  .nav-menu.active {
    left: 0;
  }

  .nav-menu a {
    display: block;
    padding: 15px;
    border-bottom: 1px solid var(--border);
  }
}

/* ===== HERO ===== */
.hero {
  min-height: 100vh;
  background: linear-gradient(135deg, var(--primary) 0%, #0C2340 100%);
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 60px 20px;
  position: relative;
  overflow: hidden;
}

.hero::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(circle at 20% 50%, rgba(249, 115, 22, 0.1), transparent),
              radial-gradient(circle at 80% 80%, rgba(16, 185, 129, 0.1), transparent);
  pointer-events: none;
}

.hero-content {
  max-width: 800px;
  text-align: center;
  position: relative;
  z-index: 2;
  animation: slideUp 0.8s ease-out;
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(40px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.hero-badge {
  display: inline-block;
  background: rgba(255, 255, 255, 0.15);
  color: white;
  padding: 10px 20px;
  border-radius: 50px;
  font-size: 14px;
  margin-bottom: 20px;
  border: 1px solid rgba(255, 255, 255, 0.3);
  backdrop-filter: blur(10px);
}

.hero-content h1 {
  font-size: 56px;
  font-weight: 900;
  margin-bottom: 20px;
  line-height: 1.2;
  text-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
}

.hero-content p {
  font-size: 20px;
  margin-bottom: 40px;
  opacity: 0.95;
}

.hero-buttons {
  display: flex;
  gap: 20px;
  justify-content: center;
  flex-wrap: wrap;
  margin-bottom: 50px;
}

.hero-stats {
  display: flex;
  gap: 40px;
  justify-content: center;
  flex-wrap: wrap;
  padding-top: 40px;
  border-top: 1px solid rgba(255, 255, 255, 0.2);
}

.stat {
  text-align: center;
}

.stat strong {
  display: block;
  font-size: 32px;
  margin-bottom: 5px;
}

/* ===== BUTTONS ===== */
.btn {
  padding: 14px 32px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 16px;
  font-weight: 700;
  transition: all 0.3s;
  text-decoration: none;
  display: inline-block;
}

.btn-primary {
  background: var(--secondary);
  color: white;
}

.btn-primary:hover {
  background: #EA580C;
  transform: translateY(-3px);
  box-shadow: 0 10px 25px rgba(249, 115, 22, 0.3);
}

.btn-outline {
  background: transparent;
  color: white;
  border: 2px solid white;
}

.btn-outline:hover {
  background: white;
  color: var(--primary);
  transform: translateY(-3px);
}

/* ===== SECTIONS ===== */
section {
  padding: 80px 0;
}

section h2 {
  font-size: 42px;
  font-weight: 900;
  margin-bottom: 60px;
  text-align: center;
  color: var(--dark);
  position: relative;
  padding-bottom: 20px;
}

section h2::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 60px;
  height: 4px;
  background: linear-gradient(90deg, var(--primary), var(--secondary));
  border-radius: 2px;
}

/* ===== ABOUT ===== */
.about {
  background: var(--light);
}

.about-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 60px;
  align-items: center;
}

.about-image {
  text-align: center;
}

.image-box {
  width: 300px;
  height: 300px;
  margin: 0 auto;
  background: linear-gradient(135deg, var(--primary), var(--secondary));
  border-radius: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 120px;
  box-shadow: var(--shadow-lg);
  animation: float 3s ease-in-out infinite;
}

@keyframes float {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-20px); }
}

.about-content h2 {
  text-align: left;
  margin-bottom: 30px;
  padding-bottom: 15px;
}

.about-content h2::after {
  left: 0;
}

.about-content p {
  color: var(--gray);
  font-size: 16px;
  line-height: 1.8;
  margin-bottom: 20px;
}

.about-features {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  margin-top: 40px;
}

.feature {
  padding: 20px;
  background: white;
  border-radius: 12px;
  text-align: center;
  border: 2px solid var(--border);
  transition: all 0.3s;
}

.feature:hover {
  border-color: var(--primary);
  transform: translateY(-5px);
  box-shadow: var(--shadow-lg);
}

.feature .icon {
  font-size: 32px;
  display: block;
  margin-bottom: 10px;
}

.feature h3 {
  color: var(--primary);
  font-size: 16px;
  margin-bottom: 5px;
}

.feature p {
  font-size: 14px;
  color: var(--gray);
  margin: 0;
}

/* ===== SERVICES ===== */
.services {
  background: white;
}

.services-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
}

.service-card {
  background: var(--light);
  padding: 40px 30px;
  border-radius: 15px;
  text-align: center;
  transition: all 0.3s;
  border: 2px solid transparent;
}

.service-card:hover {
  transform: translateY(-10px);
  background: white;
  border-color: var(--primary);
  box-shadow: var(--shadow-lg);
}

.service-icon {
  font-size: 48px;
  margin-bottom: 20px;
  display: block;
  transition: transform 0.3s;
}

.service-card:hover .service-icon {
  transform: scale(1.2) rotate(5deg);
}

.service-card h3 {
  color: var(--primary);
  font-size: 18px;
  margin-bottom: 10px;
}

.service-card p {
  color: var(--gray);
  font-size: 14px;
}

/* ===== CERTIFICATIONS ===== */
.certifications {
  background: var(--light);
}

.certs-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
}

.cert-card {
  background: white;
  padding: 30px;
  border-radius: 15px;
  text-align: center;
  border: 2px solid var(--border);
  transition: all 0.3s;
  box-shadow: var(--shadow);
}

.cert-card:hover {
  border-color: var(--primary);
  transform: translateY(-8px);
  box-shadow: var(--shadow-lg);
}

.cert-icon {
  font-size: 48px;
  margin-bottom: 15px;
  transition: transform 0.3s;
}

.cert-card:hover .cert-icon {
  transform: scale(1.3) rotate(-10deg);
}

.cert-card h3 {
  font-size: 16px;
  color: var(--dark);
  margin-bottom: 10px;
}

.cert-issuer {
  font-size: 12px;
  color: var(--primary);
  font-weight: 600;
  margin-bottom: 5px;
}

.cert-date {
  font-size: 11px;
  color: var(--gray);
}

/* ===== TRANSFORMATIONS ===== */
.transformations {
  background: white;
}

.transforms-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
}

.transform-card {
  background: var(--light);
  padding: 40px;
  border-radius: 15px;
  text-align: center;
  transition: all 0.3s;
}

.transform-card:hover {
  transform: scale(1.05);
  box-shadow: var(--shadow-lg);
}

.transform-before,
.transform-after {
  font-size: 60px;
  display: inline-block;
  margin: 0 15px;
}

.transform-arrow {
  font-size: 28px;
  color: var(--primary);
  font-weight: bold;
}

.transform-result {
  margin-top: 20px;
  color: var(--primary);
  font-weight: 700;
  font-size: 16px;
}

/* ===== TESTIMONIALS ===== */
.testimonials {
  background: linear-gradient(135deg, var(--primary), #0C2340);
  color: white;
}

.testimonials h2 {
  color: white;
}

.testimonials h2::after {
  background: var(--secondary);
}

.testimonials-slider {
  max-width: 800px;
  margin: 0 auto;
  position: relative;
  min-height: 250px;
}

.testimonial-item {
  opacity: 0;
  transition: opacity 0.5s ease-in-out;
  position: absolute;
  width: 100%;
}

.testimonial-item.active {
  opacity: 1;
  position: relative;
}

.testimonial-card {
  background: rgba(255, 255, 255, 0.1);
  padding: 40px;
  border-radius: 15px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(10px);
}

.testimonial-header {
  display: flex;
  align-items: center;
  gap: 20px;
  margin-bottom: 20px;
}

.testimonial-avatar {
  font-size: 48px;
}

.testimonial-card h3 {
  font-size: 18px;
  margin: 0;
}

.testimonial-card p {
  font-size: 14px;
  margin: 0;
}

.testimonial-stars {
  margin: 15px 0;
  font-size: 16px;
}

.testimonial-text {
  font-size: 16px;
  line-height: 1.8;
  font-style: italic;
  opacity: 0.95;
}

.testimonial-transform {
  font-size: 14px;
  margin-top: 15px;
  opacity: 0.8;
  font-weight: 600;
}

.slider-dots {
  display: flex;
  gap: 10px;
  justify-content: center;
  margin-top: 30px;
}

.dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.3);
  border: none;
  cursor: pointer;
  transition: all 0.3s;
}

.dot.active {
  background: white;
  width: 30px;
  border-radius: 6px;
}

/* ===== FAQ ===== */
.faq {
  background: var(--light);
}

.faq-container {
  max-width: 700px;
  margin: 0 auto;
}

.faq-item {
  margin-bottom: 15px;
}

.faq-question {
  width: 100%;
  background: white;
  border: 2px solid var(--border);
  padding: 20px;
  text-align: left;
  cursor: pointer;
  border-radius: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 16px;
  font-weight: 600;
  color: var(--dark);
  transition: all 0.3s;
}

.faq-question:hover {
  border-color: var(--primary);
  background: #F0F9FF;
}

.faq-question.active {
  background: var(--primary);
  color: white;
  border-color: var(--primary);
}

.faq-icon {
  transition: transform 0.3s;
  font-size: 20px;
}

.faq-question.active .faq-icon {
  transform: rotate(45deg);
}

.faq-answer {
  background: white;
  padding: 20px;
  border: 2px solid var(--border);
  border-top: none;
  border-radius: 0 0 10px 10px;
  color: var(--gray);
  font-size: 14px;
  line-height: 1.8;
}

/* ===== CONTACT ===== */
.contact {
  background: white;
}

.contact-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 60px;
}

.contact-form-wrapper h3,
.contact-info h3 {
  font-size: 22px;
  color: var(--primary);
  margin-bottom: 20px;
}

.form-embed {
  background: var(--light);
  padding: 20px;
  border-radius: 15px;
  overflow: hidden;
}

.form-embed iframe {
  border-radius: 10px;
}

.info-item {
  display: flex;
  gap: 15px;
  margin-bottom: 30px;
  padding-bottom: 20px;
  border-bottom: 1px solid var(--border);
}

.info-item:last-child {
  border-bottom: none;
}

.info-icon {
  font-size: 28px;
}

.info-item strong {
  color: var(--primary);
  display: block;
  margin-bottom: 5px;
}

.info-item p {
  color: var(--gray);
  font-size: 14px;
  margin: 0;
  line-height: 1.6;
}

.info-item a {
  color: var(--primary);
  text-decoration: none;
  font-weight: 600;
  transition: color 0.3s;
}

.info-item a:hover {
  color: var(--secondary);
}

.social-media {
  display: flex;
  gap: 15px;
  margin-top: 30px;
}

.social-icon {
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, var(--primary), var(--secondary));
  border-radius: 50%;
  text-decoration: none;
  font-size: 24px;
  transition: all 0.3s;
  color: white;
}

.social-icon:hover {
  transform: scale(1.2) rotate(10deg);
  box-shadow: var(--shadow-lg);
}

/* ===== FOOTER ===== */
.footer {
  background: var(--dark);
  color: white;
  padding: 40px 0;
  text-align: center;
  border-top: 3px solid var(--primary);
}

/* ===== RESPONSIVE ===== */
@media (max-width: 1024px) {
  .about-grid,
  .contact-grid {
    grid-template-columns: 1fr;
  }

  .services-grid,
  .transforms-grid {
    grid-template-columns: repeat(2, 1fr);
  }

  .certs-grid {
    grid-template-columns: repeat(2, 1fr);
  }

  .about-features {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 768px) {
  section {
    padding: 60px 0;
  }

  section h2 {
    font-size: 32px;
    margin-bottom: 40px;
  }

  .hero-content h1 {
    font-size: 36px;
  }

  .hero-content p {
    font-size: 16px;
  }

  .hero-buttons {
    flex-direction: column;
    gap: 15px;
  }

  .btn {
    width: 100%;
  }

  .services-grid,
  .transforms-grid,
  .certs-grid {
    grid-template-columns: 1fr;
  }

  .about-grid,
  .contact-grid {
    grid-template-columns: 1fr;
    gap: 30px;
  }

  .hero-stats {
    gap: 20px;
  }

  .stat strong {
    font-size: 24px;
  }

  .image-box {
    width: 200px;
    height: 200px;
    font-size: 80px;
  }

  .form-embed iframe {
    height: 700px !important;
  }
}

@media (max-width: 480px) {
  .hero {
    min-height: auto;
    padding: 40px 15px;
  }

  .hero-content h1 {
    font-size: 28px;
  }

  section h2 {
    font-size: 24px;
  }

  .btn {
    padding: 12px 24px;
    font-size: 14px;
  }
}
